@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}
body {
  /* background: white!important; */
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

.auth-wrapper {
  /* display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left; */
}

.auth-inner {
  max-width: 500px;
  margin: auto;
  background: #ffffff;
  /* box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2); */
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
  margin-top: 70px;
}

.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 10px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}
.float-left{
  float: left;
}
.App {
  /* text-align: center; */
  font-family: sans-serif;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
.btn-otp {
  width: 121px;
}
.text-error {
  font-size: 14px;
  text-align: center;
  color: red;
}
.color-gradiant {
  background-image: linear-gradient(to right, #7f00ff, #e100ff);
}
.text-white {
  color: white !important;
}
.text-purple {
  color: #6344f2;
}
.btn-radius {
  border-radius: 10px;
}
.btn-comman {
  width: 280px;
  height: 40px;
  border-radius: 5px;
  background-image: linear-gradient(to right, #7f00ff, #e100ff);
  color: white;
  border: none;
}
.btn-comman-small {
  width: 117px;
  height: 40px;
  border-radius: 5px;
  background-image: linear-gradient(to right, #7f00ff, #e100ff);
  color: white;
  border: none;
}
.link-comman {
  text-decoration: underline;
  color: #7f00ff;
  cursor: pointer;
}
.div-center {
  text-align: center;
  align-items: center;
}
.index {
  z-index: -1 !important;
}

.login-img-res {
  max-width: 99%;
  max-height: 99%;
}
.header-reg {
  float: left;
}
.form-verify {
  margin-top: 90px;
}
.error-contact {
  /* margin-top: 22px;
  margin-left: 0px; */
  color: red;
  font-size: 12px !important;
}
.error-div-contact {
  margin-top: 22px;
  margin-left: 16px;
  /* color: red;
  font-size: 12px !important; */
}
.hr-verify {
  border: 0;
  /* border-top: 5px; */
  height: 5px;
  width: 40px;
  margin-right: 380px;
}

.otp-time {
  margin-top: 25px !important;
}
@media only screen and (max-width: 600px) {
  .form-verify {
    margin-top: 0px;
    /* float: left; */
  }
}

.margin-pan {
  margin-top: 20px !important;
}
.div-PanEmail {
  margin-top: 90px;
}
.button-pan {
  text-align: center;
}
.modal_open {
  cursor: pointer;
  float: right;
}
.close {
  cursor: pointer;
}
.error-email {
  font-size: 12px;
  color: red;
}
.pan-error {
  font-size: 12px;
  color: black;
}
@media only screen and (max-width: 600px) {
  .margin-pan {
    margin-top: 25px !important;
  }
  .div-PanEmail {
    margin-top: 0px;
  }
}

.select {
  width: 205px;
  height: 40px;
  border-radius: 4px;
}
.hr-personal {
  border: 0;
  /* border-top: 5px; */
  height: 5px;
  width: 40px;
  /* margin-right: 380px; */
  margin-left: 0px;
}
.form-info {
  margin-top: 70px;
}
.hr-center {
  border: 0;
  /* border-top: 5px; */
  height: 5px;
  width: 40px;
  margin-left: 239px;
  margin-top: 6px;
}

.nav-item {
  margin: 12px;
}

.navbar-light {
  box-shadow: 0px 14px 80px rgb(34 35 58 / 4%);
}

.class-icons {
  margin-top: -4px;
}

.image-select {
  object-fit: contain;
}

